@import url('https://cdn.jsdelivr.net/npm/bootstrap@5.1.1/dist/css/bootstrap.min.css');
@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css');

/* Top */
.container-fluid {
  height: 100vh;
}

.btn-theme {
  border-radius: 100%;
  margin-top: 5px;
}

nav .btn {
  display: flex;
  align-items: center;
}

.form, .input-group, .d-grid {
  padding: 1em;
}

.btn-light {
  border: 1px solid #aaa;
}

.fa-sun-o {
  background-color: gold;
  border-radius: 100%;
}

/* Home */
.devices-table {
  margin-top: 50px;
}

/* About */
.p-2 p {
  font-size: 1.1rem;
}

/* Forms */
.form {
  margin-top: 50px;
}
.input-group {
  margin-top: -20px;
}

.input-group-text {
  border: 0;
}

/* Tables */
.table {
  margin-bottom: 50px;
}

th, td {
  text-align: center;
}

.exp {
  position: relative;
}

.exp-tooltip {
  display: none;
  position: absolute;
  top: 3px;
  right: -25px;
}

.exp:hover .exp-tooltip {
  display: inline-block;
}

@media only screen and (max-width: 666px) {
  .table tbody {
    margin-bottom: 3em;
  }

  .exp-tooltip {
    right: -5px;
  }

  .input-group input, .input-group select {
    width: 100%;
  }
}

@media only screen and (min-width: 992px) {
  nav {
    padding: 1em;
  }

  .app {
    padding: 0 3em;
  }

  td .btn-group {
    display: grid;
    grid-template-columns: repeat(2, 25%);
    justify-content: center;
  }
}